import { Component, OnInit } from '@angular/core';
import { StatsService } from 'src/app/services/stats.service';

@Component({
  selector: 'app-monitor',
  templateUrl: './monitor.component.html',
  styleUrls: ['./monitor.component.scss']
})
export class MonitorComponent implements OnInit {

  public fromAdm     : any;
  public toLab       : any;
  public fromLab     : any;
  public toXray      : any;
  public fromXray    : any;
  public toPharmacy  : any;
  public archived    : any;

  constructor( private statsService : StatsService) { }

  ngOnInit() {

    // monitor
    this.statsService.monitor().subscribe(
      data => { 
                this.fromAdm = data.fromAdm;
                this.toLab = data.toLab;
                this.fromLab = data.fromLab;
                this.toXray = data.toXray;
                this.fromXray = data.fromXray;
                this.toPharmacy = data.toPharmacy;
                this.archived = data.archived;
              },
      error => { console.error("Error", error); }
    );

  }

// end
}
