import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from "@angular/common/http"; 


//---------------------------------
@Injectable({
  providedIn: 'root'
})

//--------------------------------------------------------
export class UsersService {

//--------------------------------------------------------

  constructor( private http: HttpClient) { }

//Headers



//--------- MAIN URL -----------------------------
_url: string = "http://127.0.0.1:8040/";


//-------------Post Clinician Registration -----------------------------
regClinician( record : any ) {
  return this.http.post<any>(this._url + "regclinician", record)
}

//-------------Post Clinician Login -----------------------------
logClinician( record : any ) {
  return this.http.post<any>(this._url + "logclinician", record,
  { headers: new HttpHeaders({
    'Authorization': '{data}',
    'Content-Type': 'application/json',
  }), responseType: 'json'})
  }


//-------------Post Admin Registration -----------------------------
regAdmin( record : any ) {
return this.http.post<any>(this._url + "regadmin", record)
}

//-------------Post Clinician Login -----------------------------
logAdmin( record : any ) {
return this.http.post<any>(this._url + "logadmin", record)
}


//=============== THE END ==============================================
}
//--------------------------------------------------------
