import { Component, OnInit } from '@angular/core';
import { StatsService } from 'src/app/services/stats.service';
import { NotifyService } from 'src/app/services/notify.service';

@Component({
  selector: 'app-userdb',
  templateUrl: './userdb.component.html',
  styleUrls: ['./userdb.component.scss']
})
export class UserdbComponent implements OnInit {


  public clinician : any;
  public clinicianprofiles : any;
  public admin : any;
  public adminprofiles : any;

  public hideformclinician = true;
  public hideformadmin = true;

  public name : any;
  public userNo : any;
  public nationalId : any;
  public gender : any;
  public phone : any;
  public specialize : any;
  public department : any;
  public profNo : any;
  public officeNo : any;
  public mail : any;

  constructor( private statsService : StatsService,
    private notifyService : NotifyService  
  ) { }

  ngOnInit() {

// on reload
this.statsService.onreloadUSER().subscribe(
  //do nothing
);
  
  // User db
  this.statsService.userDb().subscribe(
    data => { 
              this.clinician = data.clinician;
              this.clinicianprofiles = data.clinicianprofiles;
              this.admin = data.admin;
              this.adminprofiles = data.adminprofiles;
            },
    error => { console.error("Error", error); }
  );

  }



  
// open Clinician Profile
openclinicianprofile(id){
  var userNo = { userNo : id };
  this.statsService.openuserclinician(userNo).subscribe(
    data => {
        this.name = data[0].name;
        this.userNo = data[0].userNo;
        this.nationalId = data[0].nationalId;
        this.gender = data[0].gender;
        this.phone = data[0].phone;
        this.specialize = data[0].specialize;
        this.profNo = data[0].profNo;
        this.mail = data[0].mail;


        this.hideformclinician = false;
        this.hideformadmin = true;
    },
    error => { console.error("Error", error); }
  );
};  

// open file
openadminprofile(id){
  var userNo = { userNo : id };
  this.statsService.openuseradmin(userNo).subscribe(
    data => {
        this.name = data[0].name;
        this.userNo = data[0].userNo;
        this.nationalId = data[0].nationalId;
        this.gender = data[0].gender;
        this.phone = data[0].phone;
        this.department = data[0].department;
        this.officeNo = data[0].officeNo;
        this.mail = data[0].mail;


        this.hideformclinician = true;
        this.hideformadmin = false;
    },
    error => { console.error("Error", error); }
  );
}; 

// delete clinician

deleteUSERCLI(){
  this.statsService.deleteUSERCLI().subscribe(
    data => { this.notifyService.showSuccess("User deleted!", "Success");
    setTimeout(function(){
      location.reload();
    }, 2000);
  },
    error => { console.error("Error", error); }
  );
};

// delete Admin
deleteUSERADM(){
  this.statsService.deleteUSERADM().subscribe(
    data => { this.notifyService.showSuccess("User deleted!", "Success");
    setTimeout(function(){
      location.reload();
    }, 2000);
  },
    error => { console.error("Error", error); }
  );
};



// END
}
