import { Component, OnInit } from '@angular/core';
import { FilesService } from 'src/app/services/files.service';
import { NotifyService } from 'src/app/services/notify.service';

@Component({
  selector: 'app-laboratory',
  templateUrl: './laboratory.component.html',
  styleUrls: ['./laboratory.component.scss']
})
export class LaboratoryComponent implements OnInit {

  public labPatients : any = [];
  public openedFiles : any = [];

  public toExamDisabled = true;

  public lab;

  constructor( private filesService : FilesService,
    private notifyService : NotifyService
    ) { }

  ngOnInit() {

    // Lab List patients function
    this.filesService.listLabPatient().subscribe(
      data  => { this.labPatients = data},
      error => { console.error("Error", error); }    
    );

    // page Reload function
    this.filesService.labReload().subscribe(
      // Do Nothing
    );

    // define captured data
    this.lab = {
      results : "",
    };
  }


  //=== Opening Lab file ===
  openLabFile(id){
    var patientNo = { patientNo : id };
    this.filesService.openLabFile(patientNo).subscribe(
      data => { 
                this.openedFiles = data ;
                this.notifyService.showInfo("File Opened !","Info..");
              },
      error => { console.log("Error", error);
                this.notifyService.showWarning(error.error,"Warnning!");
              }
    );
  }


//==== Change in Results Area function ======
  resultKeyup(){
    this.toExamDisabled = false;
  };

// ==== Back to Examination =========
onSubmit(){
  this.filesService.labToExam(this.lab).subscribe(
    data => {
              this.notifyService.showSuccess("Sent Back To Exam..", "Success !");
              setTimeout(function(){
                location.reload();
              }, 2000);
            },
    error => {console.log(error, "Error!")}
    
  );
}



// ========= End =============================  
}
