import { Component, OnInit } from '@angular/core';
import { FilesService } from 'src/app/services/files.service';
import { NotifyService } from 'src/app/services/notify.service';




@Component({
  selector: 'app-examination',
  templateUrl: './examination.component.html',
  styleUrls: ['./examination.component.scss']
})
export class ExaminationComponent implements OnInit {


  public admittedPatients : any = [];
  public labPatients : any = [];
  public xrayPatients : any = [];

  public openedFiles : any = [];
  public referedFiles : any = [];

  public hideFormONE = false;
  public hideFormTWO = true;

  public signReadonly = true;
  public testReadonly = true;
  public dxReadonly = true;
  public deleteDisabled = true;
  public tolabDisabled = true;
  public toxrayDisabled = true;
  public topharmacyDisabled = true;
  public topharmDisabled = true;

  public exam;
  public exam2;
  

  constructor( private filesService : FilesService,
                private notifyService : NotifyService
                ) { }
  ngOnInit() {
    // List Admitted patients functions
    this.filesService.listAdmitPatient().subscribe(
      data  => { this.admittedPatients = data},
      error => { console.error("Error", error); }    
    );
    
    // List patients from lab functions
    this.filesService.listPatientfromLab().subscribe(
      data  => { this.labPatients = data},
      error => { console.error("Error", error); }    
    );

    // List patients from xray functions
    this.filesService.listPatientfromXray().subscribe(
      data  => { this.xrayPatients = data},
      error => { console.error("Error", error); }    
    );
    

    // page Reload function
    this.filesService.examinationReload().subscribe(
      // Do Nothing
    );

    // define captured data
      this.exam = {
        signs : "",
        tests : "",
        results : "",
        dx : ""
      }
      this.exam2 = {
        dx : ""
      }
  }
    //=== Opening admission file ===
    openAdmFile(id){
      var patientNo = { patientNo : id };
      this.filesService.openAdmFile(patientNo).subscribe(
        data => { 
                  this.hideFormONE = false;
                  this.hideFormTWO = true; 
                  this.openedFiles = data ;
                  this.signReadonly = false;
                  this.deleteDisabled = false;
                  this.notifyService.showInfo("File from Admission Opened !","Info..");
                },
        error => { console.log("Error", error);
                  this.notifyService.showWarning(error.error,"Warnning!");
                }
      );
    }



//==== Change in Sign Area function ======
  signKeyup(){
    this.testReadonly = false;
    this.dxReadonly = false;
    this.deleteDisabled = true;

  };

//==== Change in Sign Area function ======
  testKeyup(){
    this.dxReadonly = true;
    this.tolabDisabled = false;
    this.toxrayDisabled = false;
  };

  //==== Change in Dx Area function ======
  dxKeyup(){
    this.testReadonly = true;
    this.signReadonly = true;
    this.deleteDisabled = true;
    this.tolabDisabled = true;
    this.toxrayDisabled = true;
    this.topharmacyDisabled = false;
  };
  dxxKeyup(){
    this.topharmDisabled = false;
  };


// ==== Delete File =======
  delete(){
    this.filesService.deleteFile().subscribe(
      data => { this.notifyService.showSuccess("File deleted!", "Success");
                setTimeout(function(){
                  location.reload();
                }, 2000);
              },
      error => {console.log(error, "Error!")}
    );
  }


// ==== to Lab =========
  toLab(){
    this.filesService.tolab(this.exam).subscribe(
      data => {
                this.notifyService.showSuccess("Sent To Lab..", "Success !");
                setTimeout(function(){
                  location.reload();
                }, 2000);
              },
      error => {console.log(error, "Error!")}
      
    );
  }


  //=== Opening Lab file ===
    openLabFile(id){
      var patientNo = { patientNo : id };
      this.filesService.openFilefromLab(patientNo).subscribe(
        data => { 
                  this.hideFormONE = true;
                  this.hideFormTWO = false; 
                  this.referedFiles = data ;
                  this.signReadonly = true;
                  this.testReadonly = true;
                  this.dxReadonly = false;
                  this.deleteDisabled = true;
                  this.tolabDisabled = true;
                  this.toxrayDisabled = true;
                  this.notifyService.showInfo(" File from Lab Opened !","Info..");
                },
        error => { console.log("Error", error);
                  this.notifyService.showWarning(error.error,"Warnning!");
                }
      );
    }










  // ==== To Xray =========
  toXray(){
    this.filesService.toxray(this.exam).subscribe(
      data => {
                this.notifyService.showSuccess("Sent To Xray.", "Success !");
                setTimeout(function(){
                  location.reload();
                }, 2000);
              },
      error => {console.log(error, "Error!")}
      
    );
  }

  //=== Opening Xray file ===
  openXrayFile(id){
    var patientNo = { patientNo : id };
    this.filesService.openFilefromXray(patientNo).subscribe(
      data => { 
                this.hideFormONE = true;
                this.hideFormTWO = false; 
                this.referedFiles = data ;
                this.signReadonly = true;
                this.testReadonly = true;
                this.dxReadonly = false;
                this.deleteDisabled = true;
                this.tolabDisabled = true;
                this.toxrayDisabled = true;
                this.notifyService.showInfo(" File from Xray Opened !","Info..");
              },
      error => { console.log("Error", error);
                this.notifyService.showWarning(error.error,"Warnning!");
              }
    );
  }




// ==== To Pharmacy =========
toPharmacy(){
  this.filesService.topharmacy(this.exam).subscribe(
    data => {
              this.notifyService.showSuccess("Sent To Pharmacy..", "Success !");
              setTimeout(function(){
                location.reload();
              }, 2000);
            },
    error => {console.log(error, "Error!")}
    
  );
}


toPharm(){
  this.filesService.topharm(this.exam2).subscribe(
    data => {
              this.notifyService.showSuccess("Sent To Pharmacy..", "Success !");
              setTimeout(function(){
                location.reload();
              }, 2000);
            },
    error => {console.log(error, "Error!")}
    
  );
}


//-------------- End ------------
}
