import { Component, OnInit } from '@angular/core';
import { FilesService } from 'src/app/services/files.service';
import { NotifyService } from 'src/app/services/notify.service';

@Component({
  selector: 'app-pharmacy',
  templateUrl: './pharmacy.component.html',
  styleUrls: ['./pharmacy.component.scss']
})
export class PharmacyComponent implements OnInit {

  public pharmacyPatients : any = [];
  public openedFiles : any = [];
  public saveDisabled = true;
  public save;

  constructor( private filesService : FilesService,
    private notifyService : NotifyService
    ) { }

  ngOnInit() {

    // List Pharm Patients function
    this.filesService.listPharmacyPatient().subscribe(
      data  => { this.pharmacyPatients = data},
      error => { console.error("Error", error); }    
    );
    // page Reload function
    this.filesService.pharmacyReload().subscribe(
      // Do Nothing
    );

  }



  //=== Opening Pharmacy file ===
  openPharmacyFile(id){
    var patientNo = { patientNo : id };
    this.filesService.openPharmacyFile(patientNo).subscribe(
      data => { 
                this.openedFiles = data ;
                this.saveDisabled = false;
                this.notifyService.showInfo("File Opened !","Info..");
              },
      error => { console.log("Error", error);
                this.notifyService.showWarning(error.error,"Warnning!");
              }
    );
  }

// ==== Save File =========
onSubmit(){
  this.filesService.saveFile(this.save).subscribe(
    data => {
              this.notifyService.showSuccess("File Saved..", "Success !");
              setTimeout(function(){
                location.reload();
              }, 2000);
            },
    error => {console.log(error, "Error!")}
    
  );
}


//======== End =======================
}
