import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { ClinicianComponent } from './clinician/clinician.component';
import { AdminComponent } from './admin/admin.component';
import { ChomeComponent } from './clinician/chome/chome.component';
import { AdmissionComponent } from './clinician/admission/admission.component';
import { ExaminationComponent } from './clinician/examination/examination.component';
import { LaboratoryComponent } from './clinician/laboratory/laboratory.component';
import { XrayComponent } from './clinician/xray/xray.component';
import { PharmacyComponent } from './clinician/pharmacy/pharmacy.component';
import { AhomeComponent } from './admin/ahome/ahome.component';
import { MonitorComponent } from './admin/monitor/monitor.component';
import { FiledbComponent } from './admin/filedb/filedb.component';
import { UserdbComponent } from './admin/userdb/userdb.component';
import { RegisterComponent } from './admin/register/register.component';
import { ClinicianGuard } from './auth/clinician.guard';
import { AdminGuard } from './auth/admin.guard';

const routes: Routes = [

  /*--------- Login Route ------------*/
  { path: "kimsapp", redirectTo: "kimsapp/login", pathMatch: "full" },
  { path: "", redirectTo: "kimsapp/login", pathMatch: "full" },


  { path: "kimsapp/login", component: LoginComponent},

  /*================ Clinical Component ============*/

  /*--------------- Home Clinician ---------------*/
  { path: "kimsapp/clinician", component: ClinicianComponent, canActivate: [ClinicianGuard],
    children: [{ path: "", component: ChomeComponent }]
  },

  /*--------------- Admission ---------------*/
  { path: "kimsapp/clinician/admission", component: ClinicianComponent, canActivate: [ClinicianGuard],
    children: [{ path: "", component: AdmissionComponent }]
  },

  /*--------------- Examination ---------------*/
  { path: "kimsapp/clinician/examination", component: ClinicianComponent, canActivate: [ClinicianGuard],
    children: [{ path: "", component: ExaminationComponent }]
  },

  /*--------------- Laboratory ---------------*/

  { path: "kimsapp/clinician/lab", component: ClinicianComponent, canActivate: [ClinicianGuard],
    children: [{ path: "", component: LaboratoryComponent }]
  },

  /*--------------- Xray ---------------*/

  { path: "kimsapp/clinician/xray", component: ClinicianComponent, canActivate: [ClinicianGuard],
    children: [{ path: "", component: XrayComponent }]
  },

  /*--------------- Pharmacy ---------------*/

  { path: "kimsapp/clinician/pharmacy", component: ClinicianComponent, canActivate: [ClinicianGuard],
    children: [{ path: "", component: PharmacyComponent }]
  },


  /*=========== Admin Component ==============*/

  /*------------ Home ---------------------------*/

  { path: "kimsapp/admin", component: AdminComponent, canActivate: [AdminGuard],
    children: [{ path: "", component: AhomeComponent }]
  },

  { path: "kimsapp/admin/monitor", component: AdminComponent, canActivate: [AdminGuard],
    children: [{ path: "", component: MonitorComponent }]
  },

  { path: "kimsapp/admin/medicalDb", component: AdminComponent, canActivate: [AdminGuard],
    children: [{ path: "", component: FiledbComponent }]
  },

  { path: "kimsapp/admin/userDb", component: AdminComponent, canActivate: [AdminGuard],
    children: [{ path: "", component: UserdbComponent }]
  },

  { path: "kimsapp/admin/register", component: AdminComponent, canActivate: [AdminGuard],
    children: [{ path: "", component: RegisterComponent }]
  },


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
