import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Route } from "@angular/router";
import { UsersService } from '../services/users.service';
import { NotifyService } from '../services/notify.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private usersService : UsersService,
    private notifyService : NotifyService
  ) { }

  public logCli;
  public logAdm;

  ngOnInit() {

    this.logCli = {
      mail : "", password : ""
    }

    this.logAdm = {
      mail : "", password : ""
    }

  }

  logClinic() {

    this.usersService.logClinician(this.logCli).subscribe(
      data => {
                window.localStorage.setItem("clinicianToken", data.token);
                window.localStorage.setItem("clinicianUser", data.signedUser);
                this.router.navigate(["/kimsapp/clinician"]);                
              },
      error => {               
                this.notifyService.showError(error.error, "Error !!"); 
                }
    );

  }

  logAdmin() {

    this.usersService.logAdmin(this.logAdm).subscribe(
      data => {
              window.localStorage.setItem("adminToken", data.token);
              window.localStorage.setItem("adminUser", data.signedUser);  
              this.router.navigate(["/kimsapp/admin"]);           
              },
      error => {               
                this.notifyService.showError(error.error, "Error !!"); 
                }
    );

  }

}
