import { Component, OnInit } from '@angular/core';
import { UsersService } from 'src/app/services/users.service';
import { NotifyService } from 'src/app/services/notify.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

//--------------------------------------------  
  constructor(
    private usersService : UsersService,
    private notifyService : NotifyService
  ) { }

//-------------------------------------------- 
  public regCli;
  public regAdm;

//-------------------------------------------- 
  ngOnInit() {

    this.regCli = {
        firstName : "", surname : "", lastName : "", nationalId : "",gender : "", 
        phone : "", specialize : "", profNo : "", mail : "", password : ""   
    }

    this.regAdm = {
        firstName : "", surname : "", lastName : "", nationalId : "", gender : "", 
        phone : "", department : "", officeNo : "", mail : "", password : ""  
    } 
  }
//------------ Clinician Registration ---------------------- 

  registerClinician(){
    this.usersService.regClinician(this.regCli).subscribe(
      data => {
        this.notifyService.showSuccess("Clinician User Added", "Successfull !!");
        setTimeout(function(){
          location.reload();
        }, 2000);
      },
      error => {
        this.notifyService.showError("Could not Register", "Failed !!");
        console.error("Error", error);
      }
    );
  };


//------------ Admin Registration ---------------------- 
  registerAdmin(){
    this.usersService.regAdmin(this.regAdm).subscribe(
      data => {
        this.notifyService.showSuccess("Admin User Added", "Successfull !!");
        setTimeout(function(){
          location.reload();
        }, 2000);
      },
      error => {
        this.notifyService.showError("Could not Register", "Failed !!");
        console.error("Error", error);
      }
    );
  };






//-------------------- end -------------------   
}
