import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from "@angular/common/http";




//--------------------------------
const httpOptions = {
  Headers: new HttpHeaders({
    "Content-type": "aplication/json"
  })
};

//-------------------------------------------------
@Injectable({
  providedIn: 'root'
})

//---------------------------------------------------
export class StatsService {


//--------- MAIN URL -----------------------------
_url: string = "http://127.0.0.1:8040/";
//--------------------------------------------------------

  constructor( private http: HttpClient ) { }

// Monitor
monitor() {
  return this.http.get<any>(this._url + "monitor");
}




// Medical Records database
medicalDb() {
  return this.http.get<any>(this._url + "medicaldb");
}

//-------------- Open medical file -----------------------------
openmedical( record : any ) {
  return this.http.post<any>(this._url + "openmedical", record);
}

//--------------- Reload MED Db ----------------------------
onreloadMED(){
  return this.http.delete<any>(this._url + "medonreload");
}

//--------------- Delete File ----------------------------
deleteMED(){
  return this.http.delete<any>(this._url + "deletemedical");
}



// User database
userDb() {
  return this.http.get<any>(this._url + "userdb");
}

//-------------- Open user Clinician -----------------------
openuserclinician( record : any ) {
  return this.http.post<any>(this._url + "openuserclinician", record);
}

//-------------- Open user Admin  -----------------------
openuseradmin( record : any ) {
  return this.http.post<any>(this._url + "openuseradmin", record);
}


//--------------- User db reload ----------------------------
onreloadUSER(){
  return this.http.delete<any>(this._url + "useronreload");
}

//--------------- Delete USER ----------------------------
deleteUSERCLI(){
  return this.http.delete<any>(this._url + "deleteusercli");
}
deleteUSERADM(){
  return this.http.delete<any>(this._url + "deleteuseradm");
}




//=============== THE END ================================
}
//------------------------------------------------------
