import { Component, OnInit } from '@angular/core';
import { StatsService } from 'src/app/services/stats.service';
import { NotifyService } from 'src/app/services/notify.service';

@Component({
  selector: 'app-filedb',
  templateUrl: './filedb.component.html',
  styleUrls: ['./filedb.component.scss']
})
export class FiledbComponent implements OnInit {

  public archivednumber : any;
  public archivedfiles : any;

  public hideform = true;

  public name : any;
  public patientNo : any;
  public age : any;
  public gender : any;
  public signs : any;
  public tests : any;
  public results : any;
  public dx : any;

  constructor( private statsService : StatsService,
                private notifyService : NotifyService  
              ) { }

  ngOnInit() {

// on reload
this.statsService.onreloadMED().subscribe(
  //do nothing
);
  
  // medical db
  this.statsService.medicalDb().subscribe(
    data => { 
              this.archivednumber = data.archivednumber;
              this.archivedfiles = data.archivedfiles;
            },
    error => { console.error("Error", error); }
  );


  }


// open file
openfile(id){
  var patientNo = { patientNo : id };
  this.statsService.openmedical(patientNo).subscribe(
    data => {
        this.name = data[0].name;
        this.patientNo = data[0].patientNo;
        this.age = data[0].age;
        this.gender = data[0].gender;
        this.signs = data[0].signs;
        this.tests = data[0].tests;
        this.results = data[0].results;
        this.dx = data[0].dx;
        this.hideform = false;
    },
    error => { console.error("Error", error); }
  );
};  

// delete file

deleteMED(){
  this.statsService.deleteMED().subscribe(
    data => { this.notifyService.showSuccess("File deleted!", "Success");
    setTimeout(function(){
      location.reload();
    }, 2000);
  },
    error => { console.error("Error", error); }
  );
};


// End
}
